const reducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_USER_DATA: {
      return {
        ...state,
      };
    }
    case SET_USER_DATA: {
      return {
        ...state,
        data: action.payload,
      };
    }
    default:
      return state;
  }
};
export default reducer;
