import { Grid, makeStyles } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import passportPhoto from "../static/images/Abhishek_koushal.jpg";
const useStyles = makeStyles((theme) => ({
  centerImage: {
    [theme.breakpoints.down("sm")]: {
      marginTop: "0px",
    },
    [theme.breakpoints.up("sm")]: {
      marginTop: "-80px",
    },
  },
}));

const Introduction = (props) => {
  const classes = useStyles();
  return (
    <>
      <Grid
        container
        item
        direction="row"
        justify="center"
        alignItems="flex-start"
        xs={12}
        className={`bg-intro-dark text-light py-md-5 py-2 ${classes.centerImage}`}
      >
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="flex-start"
          item
          id="intro"
          md={10}
          xs={12}
          className="mx-auto mx-0"
        >
          <Grid item xs={12} md={4} className={`pt-5 text-center-sm`}>
            <img
              src={passportPhoto}
              className="img-fluid rounded intro-img"
              style={{ marginBottom: "-30px", height: "300px", width: "80%" }}
            />
          </Grid>
          <Grid item xs={12} md={7} className="pt-5 mx-4">
            <div className="mb-3 text-center-sm">
              <h1 className="intro-name">{props.name}</h1>
              <p className="intro-highlight">{props.highlights.join(",")}</p>
            </div>
            <table id="introductionTable" style={{}}>
              <tbody>
                <tr>
                  <td className="text-uppercase">age:</td>
                  <td className="px-2">{props.age}</td>
                </tr>
                <tr>
                  <td className="text-uppercase">PHONE:</td>
                  <td className="px-2">
                    <a href={`tel:${props.phone}`} className="mobile">
                      {props.phone}
                    </a>
                  </td>
                </tr>
                <tr>
                  <td className="text-uppercase">EMAIL:</td>
                  <td className="px-2">
                    <a href={`mainto:${props.email}`} className="email">
                      {props.email}
                    </a>
                  </td>
                </tr>
                <tr>
                  <td className="text-uppercase">ADDRESS:</td>
                  <td className="px-2">{props.address}</td>
                </tr>
              </tbody>
            </table>
            <div className="intro-profile-icons my-2">
              <a href={props.socialLinks.github} target="_blank" className="mx-2 h5">
                <i className="fa fa-github"></i>
              </a>
              <a href={props.socialLinks.linkedin} target="_blank" className="mx-2 h5">
                <i className="fa fa-linkedin-square"></i>
              </a>
              <a href={props.socialLinks.instagram} target="_blank" className="mx-2 h5">
                <i className="fa fa-instagram"></i>
              </a>
            </div>
          </Grid>
        </Grid>
      </Grid>
    </>
  );
};
export default Introduction;
